* {
  font-family: Helvetica, Arial, Sans-serif;
}

body {
  
  padding: 60px;
}

input {
  font-size: 1.2rem;
  width: 100%;
  border-radius: 1.2 rem;
  color: white;
  background-color: green;
}

.card-grid {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;

}
.card {
  align-content: center;
  border: 1px solid grey;
  border-radius: 6px;
  margin-bottom: 10px;
  margin-right: 10px;
  overflow: hidden;
}

.card p {
  padding: 5px;
  color: green;
  text-transform: uppercase;
  text-align: center;


}
.card img {
max-width: 170px;
}